@import "../Variables.scss";

.AppPage {
	.LoadingOverlay {
		z-index: 10000;

		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;

		opacity: 1;

		background-color: $primaryBG;
		transition: background-color $t;

		&.Hidden {
			pointer-events: none;

			animation-name: LoadingOverlayDeSpawn;
			animation-duration: 750ms;
			animation-fill-mode: forwards;
		}

		@keyframes LoadingOverlayDeSpawn {
			to {
				opacity: 0;
				transform: scale(1.2);
			}
		}
	}

	.Core {
		display: flex;
		flex-direction: column;

		width: 100vw;
		height: 100vh;
	}

	.PanelMain {
		flex-grow: 1;
		overflow: hidden;

		@media screen and (max-width: 700px) {
			position: fixed;
			top: 0;
			bottom: 60px;
		}
	}

	.PanelCircles {
		position: relative;
		height: 100%;

		background-color: var(--darken-bg);

		flex-shrink: 0;

		display: flex;
		flex-direction: column;

		> .DmBubbles {
			margin-top: auto;
		}
	}

	.PanelFooter {
		position: sticky;
		bottom: 0;
		left: 0;
		right: 0;

		margin-top: auto;
		background-color: var(--darken-bg);

		flex-shrink: 0;

		.Divider {
			width: 2px;
			height: 60%;

			border-radius: 100px;
			margin: 0 15px;

			background-color: $primaryHover;

			@media screen and (max-width: 700px) {
				display: none;
			}
		}

		.LocalUserHead {
			justify-content: flex-start;
			min-width: max-content;
			width: calc(1px * var(--group-panel-size) - 15px);

			.Username {
				margin: 0;
			}

			.StatusContainer {
				font-size: 0.8em;
			}

			@media screen and (max-width: 700px) {
				display: none;
			}
		}
	}
}

.Status {
	width: 10px;
	height: 10px;
	border-radius: 100px;

	background-color: var(--txt-color);

	margin: 3px;

	&.Online {
		background-color: var(--online-color);
	}

	&.Away {
		background-color: var(--away-color);
	}

	&.Busy {
		background-color: var(--busy-color);
	}

	&.Bot {
		background-color: $primaryColor;
	}
}
