@import "../../Variables.scss";

.ChatTextAreaContainer {
	position: relative;
	flex-grow: 1;

	padding-right: 20px;
	margin-top: 2.5px;

	.ChatTextArea {
		pointer-events: none;
		opacity: 0;
	}

	.Button {
		margin: 0 5px;
		margin-top: 7px;

		cursor: pointer;

		> svg {
			padding: 1px;
			box-sizing: border-box;
		}
	}
}
