@import "../../Variables.scss";

.PanelCall {
	position: relative;
	flex-grow: 1;
	height: 100%;

	display: flex;
	align-items: center;
	justify-content: center;

	> .Members {
		flex-grow: 1;
		flex-wrap: wrap;

		> .VoiceMember {
			margin: 30px;

			display: flex;
			flex-direction: column;
			align-items: center;

			> .Username {
				text-align: center;

				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;

				max-width: 150px;
				margin-top: 5px;
			}
		}
	}
}
