@import "../../../Variables.scss";

.AvatarContainer {
	background-color: var(--darken-bg);

	> .Avatar {
		object-fit: cover;
		object-position: center;
	}

	> .DefaultAvatar {
		width: 30px;
		height: 30px;
	}
}

.UserMention {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	vertical-align: bottom;

	font-weight: bolder;

	.AvatarContainer,
	.Avatar {
		border-radius: 100%;
	}

	.Username {
		margin-left: 5px;
		color: $primaryColor;
	}
}
