@import "../../../../Variables.scss";

.ImageContainer {
	position: relative;

	min-width: 150px;
	margin: 5px 0;

	width: max-content;
	height: max-content;

	background-color: var(--darken-bg);
	cursor: pointer;

	> .Image {
		min-width: 150px;

		max-width: 50vw;
		max-height: 300px;

		object-fit: contain;
		object-position: center;
	}

	> .FailedPlaceholder {
		flex-direction: column;

		height: 150px;

		> svg {
			width: 40px;
			height: 40px;
		}

		> h4 {
			margin-bottom: 0;
		}
	}

	> .LoadingPlaceholder {
		flex-direction: column;

		width: 150px;
		height: 150px;
	}

	@media screen and (max-width: 700px) {
		width: unset;
		flex-grow: 1;

		> .Image {
			min-width: unset;
			max-width: unset;
			max-height: unset;

			width: 100%;
		}
	}
}
