@import "../../../Variables.scss";

.Group {
	> .Title {
		padding: 2px 0;

		display: flex;
		align-items: center;

		cursor: pointer;

		> svg {
			box-sizing: border-box;

			padding: 3px;
			margin: 0 2px;

			opacity: 0.75;
		}

		> .UnreadBlips {
			margin-top: -1px;
			margin-left: auto;
			margin-right: 5px;

			> .Blip {
				background-color: $primaryColor;
			}
		}

		&:hover {
			background-color: $secondaryBG;
			color: white;
		}
	}

	.FolderContents {
		margin-left: 20px;
		margin-bottom: 10px;
	}

	&.Selected > .Title {
		background-color: $primaryColor;
		color: white;
	}

	&.TypeFolder {
		> .Title {
			font-weight: bolder;
			font-size: 0.8em;
		}
	}

	&.TypeUser {
		.AvatarContainer {
			margin-right: 6px;
		}
	}
}
