@import "../../Variables.scss";

.CircleOverview {
	position: relative;

	flex-direction: column;
	flex-grow: 1;
	text-align: center;

	> .Hero {
		flex-direction: column;

		margin-top: 10vh;
		margin-bottom: auto;

		> .Circle {
			width: 150px;
			height: 150px;
		}

		> .Banner {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 20vh;

			background-color: rgba(black, 0.3);
			overflow: hidden;

			z-index: -5;

			> .BannerImage {
				width: 100%;
				height: 100%;

				object-fit: cover;

				&.Default {
					filter: blur(50px);
				}
			}
		}
	}
}
