@import "../../Variables.scss";

.CirclePage {
}

.PanelGroups {
	position: relative;
	flex-shrink: 0;
	overflow-x: hidden;
	overflow-y: auto;

	background-color: rgba(black, 0.2);

	min-width: 100px;
	width: calc(1px * var(--group-panel-size));
	max-width: 500px;

	&::-webkit-scrollbar {
		display: none;
	}

	.GroupsScaler {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;

		width: 5px;

		transition: background-color $ft;
		transition-delay: 200ms;
		cursor: col-resize;

		&:hover,
		&:active {
			background-color: var(--txt-color);
		}
	}
}
