@import "../../../Variables.scss";

.Circle {
	position: relative;
	width: 40px;
	height: 40px;

	margin: 7px;
	border: 2px solid transparent;

	flex-shrink: 0;

	background-color: var(--darken-bg);

	.Icon {
		width: 100%;
		height: 100%;

		box-sizing: border-box;

		object-fit: cover;
		object-position: center;

		&.Acronym {
			text-align: center;
			line-height: 40px;
			margin: 0;
		}
	}

	> svg {
		padding: 7px;
	}

	.TooltipArrow {
		margin-left: 7px;
	}

	> .UnreadBlips {
		position: absolute;
		top: 0;
		right: 0;
	}

	cursor: pointer;
	transition: background-color $ft, border-color $ft;

	&:hover {
		background-color: $primaryColor;
		border-color: $primaryColor;
	}
}

.UnreadBlips > .Blip {
	background-color: $primaryColor;
	color: white;

	font-weight: bolder;
	font-family: monospace;

	padding: 3px 5px;

	&.Mention {
		background-color: $secondaryColor;
	}
}
