@import "../../Variables.scss";

.UserProfileModal {
	.Renderer {
		position: relative;
	}

	> .Background {
		position: fixed;
		width: 100%;
		height: 100%;

		filter: brightness(0.6);

		> .Image {
			position: absolute;
			width: 100%;
			height: 100%;

			object-fit: cover;
			object-position: center;

			opacity: 0;
			transition: opacity $t;
		}
	}

	> .LeftSection {
		background-color: rgba(black, 0.27);
		backdrop-filter: blur(20px);
		text-align: center;

		position: relative;
		width: 290px;
		height: 100vh;

		flex-direction: column;
		align-items: center;

		overflow-x: hidden;
		overflow-y: auto;

		z-index: 10;
		transform: translate(0);

		> .RichTextArea {
			width: 80%;
		}

		> .AvatarContainer {
			margin: 20px;
		}

		> .Username {
			margin-top: -10px;
		}

		> .Feed {
			max-width: 80%;

			word-wrap: break-word;
			white-space: pre-line;
			user-select: text;
		}

		> .AboutFields {
			margin: auto 0;

			> .AboutField > .Title {
				margin-bottom: 5px;
			}
		}

		> .Button {
			margin-top: auto;
			margin-bottom: 20px;

			padding: 7px;

			border: 2px solid currentColor;
			cursor: pointer;

			&.NsfwToggle {
				color: var(--cf-red);
			}
		}
	}

	> .RightSection {
		max-height: 100%;
		color: white;

		z-index: 10;
		transform: translate(0);

		flex-grow: 1;
		flex-shrink: 1;
		flex-wrap: wrap;

		padding-right: 20px;

		overflow-x: hidden;
		overflow-y: auto;

		> div {
			width: 500px;

			> * {
				padding: 50px;
			}

			> .Header {
				padding-bottom: 0;
				margin: 0;
			}

			> .Bio {
				position: relative;
				border: none;

				word-wrap: break-word;
				white-space: pre-line;
				user-select: text;

				.Renderer {
					position: relative;
				}
			}
		}
	}

	@media screen and (max-width: 700px) {
		display: block;

		overflow-x: hidden;
		overflow-y: auto;

		text-align: center;

		.Background {
			transform: translateX(-50%);
		}

		* {
			justify-content: center;
		}

		.Bio {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			width: 80%;
		}

		> .LeftSection {
			width: 100%;
			height: max-content;
			padding-bottom: 20px;

			flex-grow: 1;
			flex-shrink: 0;
		}

		> .RightSection {
			width: 100%;
			height: max-content;
			padding-bottom: 20px;
		}

		> .CloseButton {
			width: 30px;
			height: 30px;
		}
	}
}
