@import "../Variables.scss";

.HomePage {
	display: flex;
	flex-direction: column;

	.HomeBackgroundContainer {
		pointer-events: none;
		position: absolute;

		width: 100vw;
		height: 100vh;

		overflow: hidden;
	}

	.HomeBackground {
		position: absolute;
		top: 800px;
		bottom: 100px;
		width: 1920px;

		z-index: -1;

		transform: rotateZ(165deg);
		transform-origin: bottom right;

		svg {
			position: absolute;
			color: var(--cf-bg1);

			animation-name: HomeWaveBg;
			animation-duration: 15s;
			animation-iteration-count: infinite;
			animation-timing-function: linear;

			&:last-child {
				top: 20vh;

				animation-direction: reverse;
				animation-duration: 20s;
				color: var(--cf-bg0);
			}

			@keyframes HomeWaveBg {
				from {
					transform: translateX(-25%);
				}

				to {
					transform: translateX(25%);
				}
			}
		}
	}

	.Hero {
		margin: 100px;

		@media screen and (max-width: 730px) {
			margin: auto;
			margin-top: 20px;
			padding: 30px;

			> h3 {
				font-size: 1em;
			}

			.Buttons > :last-child {
				display: none !important;
			}
		}

		> h1 {
			color: var(--cf-acc0);
		}

		> h3 {
			line-height: 2em;
		}

		.Buttons {
			display: flex;

			width: 550px;
			max-width: 90vw;

			.Button {
				display: flex;
				align-items: center;
				justify-content: center;

				padding: 7px 10px;

				> svg {
					margin-right: 7px;

					width: 20px;
					height: 20px;
				}

				&.Focus {
					background-color: var(--cf-acc0);
					color: #d2dae2;

					margin-right: 10px;
					backdrop-filter: blur(20px);
				}

				&:last-child {
					margin-left: auto;
				}

				cursor: pointer;
				transition: color $ft;

				&:hover {
					color: white;
				}
			}
		}
	}
}
