@import "../../Variables.scss";

.PanelChat {
	position: relative;
	flex-grow: 1;
	overflow: hidden;

	> .Messages {
		overflow-x: hidden;
		overflow-y: auto;

		display: flex;
		flex-direction: column-reverse;

		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
	}

	@media screen and (max-width: 700px) {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 55px;

		background-color: $primaryBG;
		z-index: 100;

		transition: transform 50ms linear;
		will-change: transform;
	}
}

.MessageGroup {
	padding: 10px;

	border-top: 4px solid rgba(black, 0.2);

	> .UserSection {
		align-items: center;

		> .Details {
			margin: 0 10px;
			font-size: 0.8em;
		}

		.Username {
			margin: 0;
			margin-bottom: 3px;

			color: $secondaryColor;
		}

		.Status {
			margin-left: 0;
		}
	}

	> .Message {
		align-items: center;

		> .TimestampContainer {
			width: 50px;
			opacity: 0;
			flex-shrink: 0;

			> .Timestamp {
				font-size: 0.8em;
				font-weight: bolder;

				color: $primaryColor;
			}
		}

		> .MessageContent {
			max-width: 90%;
			overflow: hidden;

			word-wrap: break-word;
			white-space: pre-line;
			user-select: text;
		}

		&:hover {
			background-color: rgba(black, 0.1);
			padding: 0 10px;
			margin: 0 -10px;

			> .TimestampContainer {
				opacity: 1;
			}
		}
	}

	video {
		background-color: var(--darken-bg);
		display: block;

		border: none;
		outline: none;

		margin: 10px 0;

		width: 500px;
		max-width: 80%;
		max-height: 300px;
	}

