@import "../../Variables.scss";

.SettingsModal {
	overflow-x: hidden;
	overflow-y: auto;

	> .Inner {
		position: relative;
		padding: 10vh 0;

		width: 700px;
		max-width: 90vw;

		height: 100vh;

		> .Contents {
			transition: opacity 150ms linear;

			padding-bottom: 10vh;
		}

		> .SearchField {
			width: 100%;

			border: none;
			outline: none;

			box-sizing: border-box;
			padding: 15px;
			margin: 5vh 0;

			background-color: transparent;
			color: var(--txt-color);

			border-bottom: 2px solid currentColor;

			&::placeholder {
				color: var(--txt-color);
			}

			transition: background-color $ft, color $ft;

			&:focus {
				background-color: var(--darken-bg);
				color: white;
			}
		}
	}

	.Categories {
		width: 100%;
		height: max-content;

		flex-direction: column;
		grid-gap: 20px;

		> .CategoryBubble {
			width: 700px;
			max-width: 90vw;

			background-color: var(--darken-bg);

			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: center;

			padding: 20px;
			box-sizing: border-box;

			font-size: 0.8em;

			> .FlexCenter {
				font-size: 1.5em;
			}

			.Title {
				margin: 0;
			}

			svg {
				width: 25px;
				height: 25px;

				margin-right: 5px;
			}

			> .Sections {
				opacity: 0.75;
				margin-top: 10px;
			}

			transition: background-color $ft, transform $ft;

			&:hover {
				background-color: $secondaryBG;
				transform: scale(1.01);
			}

			&:active {
				background-color: $tertiaryBG;
				transform: scale(0.99);
			}
		}
	}

	.Section {
		background-color: var(--darken-bg);
		padding: 20px;
		box-sizing: border-box;

		margin-top: 40px;

		border-top: 4px solid $primaryColor;

		> .Title {
			margin: 0;
			font-size: 1.3em;

			color: $primaryColor;
		}
	}
}

.SettingField {
	> .Title {
		margin-bottom: 10px;
	}

	> .Note {
		opacity: 0.85;

		margin-top: -5px;
		margin-bottom: 10px;
	}

	.InputField {
		border: none;
		outline: none;

		background: var(--darken-bg);
		color: var(--txt-color);

		padding: 10px;
		width: 100%;
		box-sizing: border-box;
	}
}
