@import "../../Variables.scss";

.DmsPage {
	.DirectMessageGroup {
		padding: 5px;

		font-size: 0.9em;

		> .Details {
			margin-left: 5px;
			width: 80%;

			> .Username {
				margin: 0;

				word-wrap: break-word;
				overflow: hidden;
				white-space: pre;
				text-overflow: ellipsis;

				width: 100%;
			}
		}

		cursor: pointer;

		&:hover {
			background-color: $secondaryBG;
		}
	}
}
